import 'uni-pages';wx.__webpack_require_UNI_MP_PLUGIN__ = __webpack_require__;
import Vue from 'vue'
import App from './App'
// 导入公共样式
import './styles/global.scss'
// 注册过滤器的最佳实践
import * as filters from './filters';
Object.keys(filters).forEach((key) => {
	Vue.filter(key, filters[key]);
})

// 导入store实例
import store from './store';

Vue.config.productionTip = false

App.mpType = 'app'

const app = new Vue({
	...App,
	store
})
app.$mount()


